<template>
  <div id="app">
    <HelloWorld msg="日语五十音测试工具"/>
    <DispGrid title="平假名" :list="dispHiraganas"></DispGrid>
    <DispGrid title="片假名" :list="dispKatakanas"></DispGrid>
    <h3>计时</h3>
    <p :class="timeCnt >= 60 ? 'timer timeover' : 'timer'">{{timeCnt}}</p>
    <div>
      <button @click="StartClick">{{startBtnContext}}</button>
      <!-- <button @click="StopClick">重置</button> -->
    </div>
    <p>Power By Vue CLI</p>
  </div>
</template>

<script>
// 注入组件
import HelloWorld from './components/HelloWorld.vue'
import DispGrid from './components/DispGrid.vue'

export default {
  name: 'App',
  data(){
    return {
      startBtnContext:"开始",
      isTimerRunning:null,
      timeCnt:0,
      hiragana:["あ", "い", "う", "え", "お"
              , "か", "き", "く", "け", "こ"
              , "さ", "し", "す", "せ", "そ"
              , "た", "ち", "つ", "て", "と"
              , "な", "に", "ぬ", "ね", "の"
              , "は", "ひ", "ふ", "へ", "ほ"
              , "ま", "み", "む", "め", "も"
              , "や", "ゆ", "よ"
              , "ら", "り", "る", "れ", "ろ"
              , "わ", "を"],
      katakana:["ア", "イ", "ウ", "エ", "オ"
              , "カ", "キ", "ク", "ケ", "コ"
              , "サ", "シ", "ス", "セ", "ソ"
              , "タ", "チ", "ツ", "テ", "ト"
              , "ナ", "ニ", "ヌ", "ネ", "ノ"
              , "ハ", "ヒ", "フ", "ヘ", "ホ"
              , "マ", "ミ", "ム", "メ", "モ"
              , "ヤ", "ユ", "ヨ"
              , "ラ", "リ", "ル", "レ", "ロ"
              , "ワ", "ヲ"],
      dispHiraganas:[],
      dispKatakanas:[]
    }
  },
  components: {
    HelloWorld,
    DispGrid
  },
  methods:{
    StartClick(){
      if(this.startBtnContext == "开始"){
        this.timeCnt=0;
        this.dispHiraganas = this.genRandomArray(this.hiragana);
        this.dispKatakanas = this.genRandomArray(this.katakana);
        
        this.isTimerRunning = setInterval(
          () => {this.timeCnt++;}, 1000);
        this.startBtnContext = "停止";
      } else {
        clearInterval(this.isTimerRunning);
        this.startBtnContext = "开始";
      }
    },
    genRandomArray(orgKanaArray){
      var i = orgKanaArray.length;
      var remainKataKanas = [];
      var dispKanaArray = [];
      while(i){
        i--;
        remainKataKanas = orgKanaArray.filter(item => !dispKanaArray.includes(item));

        dispKanaArray[i]=remainKataKanas[Math.round(Math.random()*i)];
      }
      return dispKanaArray;
    },
    StopClick(){
      clearInterval(this.isTimerRunning);
      this.dispHiraganas = []
      this.dispKatakanas = []
      this.timeCnt = 0;
      this.startBtnContext = "开始";
    },
  }
}
</script>

